import React, {Component} from 'react';
import './index.scss';
import '../../../style/style.scss';
import {Button, Animate} from 'zzc-design-mobile';

export default class App extends Component {

    constructor( props ) {
        super( props );
        this.state = {
            visible: false
        };
    }

    showAnimate () {
        this.setState( {
            visible: true
        } );
    }

    closeCallback( type ) {
        console.log( type );
    }

    render () {
        const { visible } = this.state;
        return (
            <div className='zzc-demo'>
                <div className='zzc-demo-header'>
                    <h1 className='zzc-demo-title'>Animate </h1>
                </div>
                <div className='zzc-demo-body'>
                    <Button onClick={this.showAnimate}></Button>
                    <Animate
                        visible={visible}
                        animationName={{
                            enter: 'zzc-zoom-enter',
                            enterActive: 'zzc-zoom-enter-active',
                            leave: 'zzc-zoom-leave',
                            leaveActive: 'zzc-zoom-leave-active'
                        }}
                        onEnd={( type ) => {
                            this.closeCallback( type );
                        }}
                    >
                        <div></div>
                    </Animate>
                </div>
            </div>

        );
    }
}


